import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from 'material-ui';

export default class DeleteTagDialog extends Component {

  state = {
    show: false,
    name: '',
  };

  changeDialogState = (name) => {
    this.setState({ show: !this.state.show, name });
  };

  hideDialog = () => {
    this.setState({ show: !this.state.show });
  };

  deleteTag = () => {
    this.props.delete(this.state.name);
    this.hideDialog();
  };

  render() {
    return (
      <Dialog
        open={this.state.show}
        onClose={this.hideDialog}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Delete #{this.state.name}?</DialogTitle>
        <DialogContent>
          <DialogContentText>
            After deletion, you will lose all data related to this tag and also you won't be able to use it.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.hideDialog} style={{ color: window.tagMainColor }}>
            Cancel
          </Button>
          <Button onClick={this.deleteTag} style={{ color: '#F44336' }}>
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

DeleteTagDialog.propTypes = {
  delete: PropTypes.func.isRequired,
};



